import {Component} from '@angular/core';
import {IonicPage, NavController, NavParams,LoadingController,Platform,AlertController } from 'ionic-angular';
import {GoogleAnalytics} from "@ionic-native/google-analytics";
import {ApiProvider} from "../../providers/api/api";

declare var window: any

/**
 * Generated class for the ServicesPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@Component({
    selector: 'page-services',
    templateUrl: 'services.html',
})
export class ServicesPage {

    public networkConnected = true;
    loading: any;

    constructor(public navCtrl: NavController, public navParams: NavParams, public ga: GoogleAnalytics,public apiProvider: ApiProvider,public platform: Platform,public loadingCtrl: LoadingController,public alertCtrl: AlertController) {

        this.platform.ready().then(() => {

            if (window.cordova && (window.navigator.connection.type === window.Connection.UNKNOWN || window.navigator.connection.type === window.Connection.NONE)) {
                this.networkConnected = false;
            } else {
                this.networkConnected = true;
            }

        });
    }

    ionViewDidLoad() {
        console.log('ionViewDidLoad ServicesPage');
        this.ga.trackView("Services Screen");
        this.presentLoading();

        this.apiProvider.trackPage("Services Screen")
            .then((res) => {

                }, (err) => {

                }
            );
    }

    presentLoading() {

        this.loading = this.loadingCtrl.create({
            content: 'Loading...'
        });

        this.loading.present();

        setTimeout(() => {
            this.loading.dismiss();
            if(!this.networkConnected){
                this.showAlert();
            }
        }, 2000);

    }

    showAlert() {

        let alert = this.alertCtrl.create({
            title: 'ERROR',
            message: "Please check your internet connection and try again.",
            cssClass: 'childNoticeAlert',
            buttons: [
                {
                    text: 'Ok',
                    handler: data => {
                        //this.navCtrl.push('InviteUsersPage');
                    }
                }

            ]

        });
        alert.present();
    }


}



