import {Component, ViewChild} from '@angular/core';
import {Platform, Nav} from 'ionic-angular';
import {StatusBar} from '@ionic-native/status-bar';
import {SplashScreen} from '@ionic-native/splash-screen';

import {WelcomePage} from '../pages/welcome/welcome';
import {MyChildrenPage} from "../pages/my-children/my-children";
import {InvitedFamilyPage} from "../pages/invited-family/invited-family";
import {FaqPage} from "../pages/faq/faq";
import {AboutStudyPage} from "../pages/about-study/about-study";
import {ContactPage} from "../pages/contact/contact";
import {SettingsPage} from "../pages/settings/settings";
import {FeedbackPage} from "../pages/feedback/feedback";
import {AcknowledgementPage} from "../pages/acknowledgement/acknowledgement";

import {Splashscreen2Page} from "../pages/splashscreen2/splashscreen2";
import {GlobalProvider} from "../providers/global/global";
import {ScreenOrientation} from '@ionic-native/screen-orientation';
import {AppVersion} from '@ionic-native/app-version';
import { Push, PushObject, PushOptions,NotificationEventResponse } from '@ionic-native/push';
import {GoogleAnalytics} from "@ionic-native/google-analytics";
import { NotificationsProvider} from "../providers/notifications/notifications";

declare var window: any;

@Component({
    templateUrl: 'app.html'
})
export class MyApp {

    public parentImg;
    public parentFullName;
    public appVersions = "1.0";


    @ViewChild(Nav) nav: Nav;
    rootPage: any = Splashscreen2Page;

    constructor(public platform: Platform, public statusBar: StatusBar, private ga: GoogleAnalytics,public appVersion: AppVersion, splashScreen: SplashScreen, public globalProvider: GlobalProvider,public notificationProvider: NotificationsProvider, public screenOrientation: ScreenOrientation, private push: Push) {
        platform.ready().then(() => {
            // Okay, so the platform is ready and our plugins are available.
            // Here you can do any higher level native things you might need.
            statusBar.styleDefault();
            splashScreen.hide();
            this.notificationProvider.init();
            //screenOrientation.lock(this.screenOrientation.ORIENTATIONS.PORTRAIT);

            this.ga.startTrackerWithId('UA-115566225-1')
                .then(() => {
                    console.log('Google analytics is ready now');
                    //the component is ready and you can call any method here
                    this.ga.debugMode();
                    this.ga.setAllowIDFACollection(true);
                })
                .catch(e => console.log('Error starting GoogleAnalytics', e));
        });


        platform.registerBackButtonAction(null, 101) // disable android back button


        if (window.localStorage.getItem("parentPhoto") != null) {
            this.parentImg = window.localStorage.getItem("parentPhoto");
        } else {
            this.parentImg = "assets/imgs/parent.png";
        }

        if (window.localStorage.getItem("parentFirstName") != null && window.localStorage.getItem("parentLastName") != null) {
            //globalProvider.parentFirstName = window.localStorage.getItem("parentFirstName");
            //globalProvider.parentLastName = window.localStorage.getItem("parentLastName");

            this.parentFullName = window.localStorage.getItem("parentFirstName") + " " + window.localStorage.getItem("parentLastName");

        }


    }

    // pushSetup(){
    //     const options: PushOptions = {
    //         android: {
    //             senderID:'586452150795'
    //         },
    //         ios: {
    //             alert: true,
    //             badge: true,
    //             sound: true,
    //             fcmSandbox: true
    //         },
    //     };
    //
    //     const pushObject: PushObject = this.push.init(options);
    //
    //
    //     pushObject.on('notification').subscribe((notification: any) => console.log('Received a notification', notification));
    //
    //     pushObject.on('registration').subscribe((registration: any) => console.log('Device registered', registration));
    //
    //     pushObject.on('error').subscribe(error => console.error('Error with Push plugin', error));
    // }

    myChildren() {
        this.nav.push(MyChildrenPage);
    }

    myFamily() {
        this.nav.push(InvitedFamilyPage);

    }

    settings() {
        this.nav.push(SettingsPage);
    }

    feedback() {
        this.nav.push(FeedbackPage);

    }

    contactUs() {
        this.nav.push(ContactPage);
    }

    aboutStudy() {
        this.nav.push(AboutStudyPage);
    }

    faq() {
        this.nav.push(FaqPage);
    }

    acknowledgement() {
        this.nav.push(AcknowledgementPage);
    }
}




