import {HttpClient} from '@angular/common/http';
import {Injectable} from '@angular/core';

/*
  Generated class for the GlobalProvider provider.

  See https://angular.io/guide/dependency-injection for more info on providers
  and Angular DI.
*/
@Injectable()
export class GlobalProvider {

    public staging_url = "https://stage.api.seehowtheygrow.nihi.auckland.ac.nz";
    //public production_url = "https://api.seehowtheygrow.nihi.auckland.ac.nz";

    public UUID;
    public parentFirstName;
    public parentLastName;
    public parentEmail;
    public parentMobileNumber;
    public parentInvitationCode;
    public parentRelationshipToChild;
    public parentEthnicity;
    public parentGender;
    public parentAge;
    public parentRegion;
    public parentPostcode;
    public parentRegisterDateTime;
    public parentCSRF_Token;
    public parentImage;

    public familyId;

    public notificationToken;
    public notifications: number = 0;


    public childWeightPlotsByParent = [];
    public childLengthPlotsByParent = [];
    public childHeadPlotsByParent = [];
    public childWeightPlotsByHealth = [];
    public childLengthPlotsByHealth = [];
    public childHeadPlotsByHealth = [];

    public healthProWeightPlots = [];


    public childFirstName;
    public childDateBirth;
    public childWeeksBirth;
    public childSex;
    public childHousehold;
    public childId;

    public inviteeEmail;
    public inviteeName;

    public percentileWeight;
    public percentileLength;
    public percentileHead;

    public feedbackFeature;

    public newNotificitations = false;



    constructor(public http: HttpClient) {
        console.log('Hello GlobalProvider Provider');
    }

    validation_messages = {
        'firstName': [
            {type: 'required', message: 'First name is required.'},
            {type: 'minlength', message: 'First name must be at least 2 characters long.'},
            {type: 'maxlength', message: 'First name cannot be more than 25 characters long.'},
        ],

        'name': [
            {type: 'required', message: 'Name is required.'},
            {type: 'minlength', message: 'Name must be at least 2 characters long.'},
            {type: 'maxlength', message: 'Name cannot be more than 25 characters long.'},
        ],

        'lastName': [
            {type: 'required', message: 'Last name is required.'},
            {type: 'minlength', message: 'Last name must be at least 2 characters long.'},
            {type: 'maxlength', message: 'Last name cannot be more than 25 characters long.'},
        ],

        'email': [
            {type: 'required', message: 'Email is required.'},
            {type: 'pattern', message: 'Enter a valid email.'},
        ],

        'phone': [
            {type: 'required', message: 'Phone is required.'},
            {type: 'pattern', message: 'Phone incorrect format'}
        ],

        'invitationCode': [
            {type: 'required', message: 'Invitation code is required.'},
            {type: 'pattern', message: 'Incorrect format '}
        ],

        'postCode': [
            {type: 'required', message: 'Post code is required.'},
            {type: 'pattern', message: 'Incorrect format '}
        ],

        'terms': [
            {type: 'pattern', message: 'You must accept terms and conditions.'}
        ],
        'subject': [
            {type: 'required', message: 'Subject is required.'},
            {type: 'minlength', message: 'Subject must be at least 2 characters long.'},
        ],
        'message': [
            {type: 'required', message: 'Message is required..'},
            {type: 'minlength', message: 'Message must be at least 2 characters long.'},
        ]
    };





}



