import {Component} from '@angular/core';
import {
    IonicPage,
    NavController,
    NavParams,
    MenuController,
    LoadingController,
    Platform,
    AlertController
} from 'ionic-angular';
import {GlobalProvider} from "../../providers/global/global";
import {ApiProvider} from "../../providers/api/api";
import {GoogleAnalytics} from "@ionic-native/google-analytics";
import {MemoriesListPage} from "../memories-list/memories-list";
import {MemoriesPage} from "../memories/memories";
import {ResourcesPage} from "../resources/resources";
import {SettingsPage} from "../settings/settings";
import {GrowthChartPage} from "../growth-chart/growth-chart";
import {NotificationsPage} from "../notifications/notifications";

declare var window: any


/**
 * Generated class for the FeedbackPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@Component({
    selector: 'page-feedback',
    templateUrl: 'feedback.html',
})
export class FeedbackPage {

    public q1Answer;
    public q2Answer;
    public q3Answer;
    public q4Answer;
    public featureAnswer: any;
    public anyFeatureChanges;
    public anythingElseChanges;

    public message1: string = '';
    public message2: string = '';

    public features: any;
    //public Q1Hide = false;

    public networkConnected = true;
    loading: any;


    constructor(public navCtrl: NavController,public globalProvider:GlobalProvider, public platform: Platform, public navParams: NavParams, public apiProvider: ApiProvider, public ga: GoogleAnalytics, public loadingController: LoadingController, public menuCtrl: MenuController, public alertCtrl: AlertController) {

        this.platform.ready().then(() => {

            if (window.cordova && (window.navigator.connection.type === window.Connection.UNKNOWN || window.navigator.connection.type === window.Connection.NONE)) {
                this.networkConnected = false;
            } else {
                this.networkConnected = true;
            }

        });

    }

    ionViewWillEnter() {
        console.log('ionViewDidLoad FeedbackPage');
        this.ga.trackView("Feedback Screen");

        /////////////////////////////// check new notifications available ///////////////////////////////////

        window.localStorage.removeItem("unreadNotifications");

        if (this.networkConnected === false) {
            this.showError("Check your internet connection and try again.");
        } else {
            /////// track page //////
            this.apiProvider.trackPage("Feedback Screen")
                .then((res) => {

                    }, (err) => {

                    }
                );

            this.apiProvider.getNotifications()
                .then((res) => {
                        if(window.localStorage.getItem("unreadNotifications") == 'yes'){
                            this.globalProvider.newNotificitations = true;
                        }else{
                            this.globalProvider.newNotificitations = false;
                        }

                    }, (err) => {
                        //this.loading.dismiss();
                        this.showError(err);
                        //this.navCtrl.push(ChildRegistrationPage);
                    }
                );
        }


        //////////////////////////////////////////////////////////////////////////

    }

    toggleMenu() {
        this.menuCtrl.toggle();
    }

    disableOtherButtonsQ2(value) {

        (<HTMLInputElement> document.getElementById('q2Answer1')).disabled = false;
        (<HTMLInputElement> document.getElementById('q2Answer2')).disabled = false;
        (<HTMLInputElement> document.getElementById('q2Answer3')).disabled = false;
        (<HTMLInputElement> document.getElementById('q2Answer4')).disabled = false;
        (<HTMLInputElement> document.getElementById('q2Answer5')).disabled = false;
        if (value == 'strongly-disagree') {
            (<HTMLInputElement> document.getElementById('q2Answer1')).disabled = true;

        }
        else if (value == 'disagree') {

            (<HTMLInputElement> document.getElementById('q2Answer2')).disabled = true;

        }
        else if (value == 'neutral') {

            (<HTMLInputElement> document.getElementById('q2Answer3')).disabled = true;

        }
        else if (value == 'agree') {

            (<HTMLInputElement> document.getElementById('q2Answer4')).disabled = true;

        }
        else if (value == 'strongly-agree') {

            (<HTMLInputElement> document.getElementById('q2Answer5')).disabled = true;
        }
    }

    disableOtherButtonsQ4(value) {

        (<HTMLInputElement> document.getElementById('q4Answer1')).disabled = false;
        (<HTMLInputElement> document.getElementById('q4Answer2')).disabled = false;
        (<HTMLInputElement> document.getElementById('q4Answer3')).disabled = false;
        (<HTMLInputElement> document.getElementById('q4Answer4')).disabled = false;
        (<HTMLInputElement> document.getElementById('q4Answer5')).disabled = false;
        if (value == 'strongly-disagree') {
            (<HTMLInputElement> document.getElementById('q4Answer1')).disabled = true;

        }
        else if (value == 'disagree') {

            (<HTMLInputElement> document.getElementById('q4Answer2')).disabled = true;

        }
        else if (value == 'neutral') {

            (<HTMLInputElement> document.getElementById('q4Answer3')).disabled = true;

        }
        else if (value == 'agree') {

            (<HTMLInputElement> document.getElementById('q4Answer4')).disabled = true;

        }
        else if (value == 'strongly-agree') {

            (<HTMLInputElement> document.getElementById('q4Answer5')).disabled = true;
        }
    }

    disableOtherButtonsQ1(value) {

        (<HTMLInputElement> document.getElementById('q1Answer1')).disabled = false;
        (<HTMLInputElement> document.getElementById('q1Answer2')).disabled = false;
        (<HTMLInputElement> document.getElementById('q1Answer3')).disabled = false;
        (<HTMLInputElement> document.getElementById('q1Answer4')).disabled = false;
        (<HTMLInputElement> document.getElementById('q1Answer5')).disabled = false;
        if (value == 'strongly-disagree') {
            (<HTMLInputElement> document.getElementById('q1Answer1')).disabled = true;

        }
        else if (value == 'disagree') {

            (<HTMLInputElement> document.getElementById('q1Answer2')).disabled = true;

        }
        else if (value == 'neutral') {

            (<HTMLInputElement> document.getElementById('q1Answer3')).disabled = true;

        }
        else if (value == 'agree') {

            (<HTMLInputElement> document.getElementById('q1Answer4')).disabled = true;

        }
        else if (value == 'strongly-agree') {

            (<HTMLInputElement> document.getElementById('q1Answer5')).disabled = true;
        }
    }

    disableOtherButtonsQ3(value) {

        (<HTMLInputElement> document.getElementById('q3Answer1')).disabled = false;
        (<HTMLInputElement> document.getElementById('q3Answer2')).disabled = false;
        (<HTMLInputElement> document.getElementById('q3Answer3')).disabled = false;
        (<HTMLInputElement> document.getElementById('q3Answer4')).disabled = false;
        (<HTMLInputElement> document.getElementById('q3Answer5')).disabled = false;
        if (value == 'strongly-disagree') {
            (<HTMLInputElement> document.getElementById('q3Answer1')).disabled = true;

        }
        else if (value == 'disagree') {

            (<HTMLInputElement> document.getElementById('q3Answer2')).disabled = true;

        }
        else if (value == 'neutral') {

            (<HTMLInputElement> document.getElementById('q3Answer3')).disabled = true;

        }
        else if (value == 'agree') {

            (<HTMLInputElement> document.getElementById('q3Answer4')).disabled = true;

        }
        else if (value == 'strongly-agree') {

            (<HTMLInputElement> document.getElementById('q3Answer5')).disabled = true;
        }
    }

    q1(value) {
        this.q1Answer = value;
        //this.Q1Hide = true;

        //alert("dddddd"+this.q1Answer);
        this.disableOtherButtonsQ1(value);
    }

    q2(value) {
        this.q2Answer = value;
        // alert("eeeeeee"+this.q2Answer);
        this.disableOtherButtonsQ2(value);
    }

    q3(value) {
        this.q3Answer = value;
        //alert("ffffff"+this.q3Answer);
        this.disableOtherButtonsQ3(value);
    }

    q4(value) {
        this.q4Answer = value;
        //alert("ggggggg"+this.q4Answer);
        this.disableOtherButtonsQ4(value);
    }

    featureChange(value) {
        this.featureAnswer = value;
        // alert("hhhhhh"+ this.featureAnswer);
        // this.features = this.featureAnswer.split(',');

    }

    anyFeatureChange(value) {
        this.anyFeatureChanges = value;
        //alert("kkkkkkk"+this.anyFeatureChanges);
    }

    anythingElseChange(value) {
        this.anythingElseChanges = value;
    }

    feedbackSubmit() {

        if (this.networkConnected === false) {
            this.showAlert("Check your internet connection and try again.");
        } else {
            this.presentLoading();

            ///// contact us submit /////////
            setTimeout(() => {
                this.apiProvider.addFeedback(this.q1Answer, this.q2Answer, this.q3Answer, this.q4Answer, this.featureAnswer, this.anyFeatureChanges, this.anythingElseChanges)
                    .then((res) => {
                            this.loading.dismiss();
                            this.showAlert("Thanks for your feedback!");
                            this.message1 = '';
                            this.message2 = '';

                            //this.navCtrl.push(InviteUsersPage);
                        }, (err) => {
                            this.loading.dismiss();
                            this.showError(err);
                            //this.navCtrl.push(ChildRegistrationPage);
                        }
                    );
            }, 1000);
        }

    }

    presentLoading() {

        this.loading = this.loadingController.create({
            content: 'Please wait...'
        });

        this.loading.present();

    }


    showAlert(msg) {

        let alert = this.alertCtrl.create({
            title: 'SHTG',
            message: msg,
            cssClass: 'childNoticeAlert',
            buttons: [
                {
                    text: 'Ok',
                    handler: data => {
                        //this.navCtrl.push('InviteUsersPage');
                    }
                }

            ]

        });
        alert.present();
    }

    showError(err) {

        let alert = this.alertCtrl.create({
            title: 'ERROR',
            message: err.error,
            cssClass: 'childNoticeAlert',
            buttons: [
                {
                    text: 'Ok',
                    handler: data => {
                        //this.navCtrl.push('InviteUsersPage');
                    }
                }

            ]

        });
        alert.present();

    }

    clickedMemory() {
        if (window.localStorage.getItem('memoryAdded')) {
            this.navCtrl.push(MemoriesListPage);
        } else {
            this.navCtrl.push(MemoriesPage);
        }
    }

    clickedResources() {
        this.navCtrl.push(ResourcesPage);
    }

    clickedSettings() {
        this.navCtrl.push(SettingsPage);
    }

    clickedChart() {
        this.navCtrl.setRoot(GrowthChartPage);
    }

    clickedNotifications() {
        this.navCtrl.push(NotificationsPage);
    }

}



