    showAlert(err) {

        let alert = this.alertCtrl.create({
            title: 'ERROR',
            message: err.message,
            cssClass: 'childNoticeAlert',
            buttons: [
                {
                    text: 'Ok',
                    handler: data => {
                        //this.navCtrl.push('InviteUsersPage');
                    }
                }

            ]

        });
        alert.present();
    }


    showError(err) {

        let alert = this.alertCtrl.create({
            title: 'ERROR',
            message: err,
            cssClass: 'childNoticeAlert',
            buttons: [
                {
                    text: 'Ok',
                    handler: data => {
                        //this.navCtrl.push('InviteUsersPage');
                    }
                }

            ]

        });
        alert.present();
    }

    showNotification(err) {

        let alert = this.alertCtrl.create({
            title: 'SHTG',
            message: err,
            cssClass: 'childNoticeAlert',
            buttons: [
                {
                    text: 'Cancel',
                    handler: data => {


                    }
                },
                {
                    text: 'Confirm',
                    handler: data => {
                        this.saveMeasurementsToDatabase();
                    }
                }


            ]

        });
        alert.present();
    }
