import {Component} from '@angular/core';
import {IonicPage, NavController, NavParams} from 'ionic-angular';
import {GrowthChartPage} from "../growth-chart/growth-chart";
import {FirstAidDangerPage} from "../first-aid-danger/first-aid-danger";
import {FirstAidCprPage} from "../first-aid-cpr/first-aid-cpr";
import {FirstAidFirstPage} from "../first-aid-first/first-aid-first";
import {GoogleAnalytics} from "@ionic-native/google-analytics";
import {ApiProvider} from "../../providers/api/api";

/**
 * Generated class for the FirstAidPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@Component({
    selector: 'page-first-aid',
    templateUrl: 'first-aid.html',
})
export class FirstAidPage {

    constructor(public navCtrl: NavController, public navParams: NavParams, public ga: GoogleAnalytics, public apiProvider: ApiProvider) {
    }

    ionViewDidLoad() {
        console.log('ionViewDidLoad FirstAidPage');
        this.ga.trackView("First Aid Screen");

        this.apiProvider.trackPage("First Aid Screen")
            .then((res) => {

                }, (err) => {

                }
            );
    }

    danger() {
        this.navCtrl.push(FirstAidDangerPage);

    }

    cpr() {
        this.navCtrl.push(FirstAidCprPage);

    }

    firstAids() {
        this.navCtrl.push(FirstAidFirstPage);

    }
}



