import {Component} from '@angular/core';
import {
    IonicPage,
    NavController,
    LoadingController,
    NavParams,
    Platform,
    AlertController,
    MenuController
} from 'ionic-angular';
import {InviteUsersPage} from "../invite-users/invite-users";
import {GrowthChartPage} from "../growth-chart/growth-chart";
import {NotificationsPage} from "../notifications/notifications";
import {GoogleAnalytics} from "@ionic-native/google-analytics";
import {MemoriesListPage} from "../memories-list/memories-list";
import {MemoriesPage} from "../memories/memories";
import {ResourcesPage} from "../resources/resources";
import {SettingsPage} from "../settings/settings";
import {ApiProvider} from "../../providers/api/api";
import { GlobalProvider} from "../../providers/global/global";

declare var window: any


/**
 * Generated class for the InvitedFamilyPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@Component({
    selector: 'page-invited-family',
    templateUrl: 'invited-family.html',
})
export class InvitedFamilyPage {

    public removeUser = 'remove-circle';
    public userStatus = 'active';
    public inviteUserHide = false;
    public networkConnected = true;
    loading: any;

    public invidedUsersList: Array<{ first_name: string, last_name: string, user_picture: string, uid: string }> = [];


    constructor(public navCtrl: NavController,public globalProvider: GlobalProvider, public apiProvider: ApiProvider, public platform: Platform, public loadingCtrl: LoadingController, public navParams: NavParams, public alertCtrl: AlertController, public menuCtrl: MenuController, public ga: GoogleAnalytics) {

        this.platform.ready().then(() => {

            if (window.cordova && (window.navigator.connection.type === window.Connection.UNKNOWN || window.navigator.connection.type === window.Connection.NONE)) {
                this.networkConnected = false;
            } else {
                this.networkConnected = true;
            }

        });

    }

    ionViewWillEnter() {
        this.ga.trackView("Invited Family Screen");

        /////////////////////////////// check new notifications available ///////////////////////////////////

        window.localStorage.removeItem("unreadNotifications");


        if (this.networkConnected === false) {
            this.showError("Check your internet connection and try again.");
        } else {
            /////// track page //////
            this.apiProvider.trackPage("Invited Family Screen")
                .then((res) => {

                    }, (err) => {

                    }
                );

            this.apiProvider.getNotifications()
                .then((res) => {
                        if(window.localStorage.getItem("unreadNotifications") == 'yes'){
                            this.globalProvider.newNotificitations = true;
                        }else{
                            this.globalProvider.newNotificitations = false;
                        }

                    }, (err) => {
                        //this.loading.dismiss();
                        this.showError(err);
                        //this.navCtrl.push(ChildRegistrationPage);
                    }
                );
        }


        //////////////////////////////////////////////////////////////////////////

        console.log('ionViewDidLoad InvitedFamilyPage');
        if (window.localStorage.getItem("primaryUser") === "true") {
            this.inviteUserHide = false;
        } else {
            this.inviteUserHide = true;
        }


        if (this.networkConnected === false) {
            this.showError("Check your internet connection and try again.");
        } else {

            this.presentLoading();

            setTimeout(() => {
                this.apiProvider.getSecondaryUsers(window.localStorage.getItem("familyId"))
                    .then((res) => {
                            console.log("secondary users list" + JSON.stringify(res));
                            for (let i = 0; i < JSON.parse(JSON.stringify(res)).length; i++) {
                                this.invidedUsersList.push(
                                    {
                                        first_name: JSON.parse(JSON.stringify(res))[i].field_first_name,
                                        last_name: JSON.parse(JSON.stringify(res))[i].field_last_name,
                                        user_picture: JSON.parse(JSON.stringify(res))[i].user_picture,
                                        uid: JSON.parse(JSON.stringify(res))[i].uid
                                    });
                            }

                            //window.localStorage.setItem("childrenList", JSON.stringify(this.childrenList));
                            this.loading.dismiss();


                        }, (err) => {

                            this.loading.dismiss();
                            //this.showError(err);
                            //this.navCtrl.push(ChildRegistrationPage);
                        }
                    );
            }, 1000);


        }

    }

    presentLoading() {

        this.loading = this.loadingCtrl.create({
            content: 'Please wait...'
        });

        this.loading.present();

    }

    inviteFamily() {
        this.navCtrl.push(InviteUsersPage);
    }

    deleteFamily(user) {
        if (this.removeUser == "remove-circle") {
            this.deleteUserPopup(user.uid);
        } else if (this.removeUser == "add-circle") {
            this.removeUser = 'remove-circle';
            this.userStatus = 'de-active';
        }
    }

    toggleMenu() {
        this.menuCtrl.toggle();
    }

    showError(err) {

        let alert = this.alertCtrl.create({
            title: 'ERROR',
            message: err,
            cssClass: 'childNoticeAlert',
            buttons: [
                {
                    text: 'Ok',
                    handler: data => {
                        //this.navCtrl.push('InviteUsersPage');
                    }
                }

            ]

        });
        alert.present();
    }

    deleteUserPopup(uid) {

        let alert = this.alertCtrl.create({
            title: 'NOTICE',
            message: 'Are you sure you want to deactivate this users access? You can reactivate it at any time.',
            cssClass: 'chartSkipAlert',
            enableBackdropDismiss: false,
            buttons: [
                {
                    text: 'Yes',
                    handler: data => {

                        //this.navCtrl.push('InviteUsersPage');


                        if (this.networkConnected === false) {
                            this.showError("Check your internet connection and try again.");
                        } else {

                            //this.presentLoading();

                            setTimeout(() => {
                                this.apiProvider.deleteSecondaryUser(uid)
                                    .then((res) => {
                                            console.log("secondary users deleted");
                                            this.userStatus = 'de-active';
                                            this.removeUser = 'add-circle';

                                        }, (err) => {
                                        console.log("secondary users deleted error"+JSON.stringify(err));

                                            //this.loading.dismiss();
                                            //this.showError(err);
                                            //this.navCtrl.push(ChildRegistrationPage);
                                        }
                                    );
                            }, 1000);


                        }


                    }
                },
                {
                    text: 'No',
                    handler: data => {
                        //this.navCtrl.push('InviteUsersPage');
                    }
                }

            ]

        });
        alert.present();

    }


    clickedMemory() {
        if (window.localStorage.getItem('memoryAdded')) {
            this.navCtrl.push(MemoriesListPage);
        } else {
            this.navCtrl.push(MemoriesPage);
        }
    }

    clickedResources() {
        this.navCtrl.push(ResourcesPage);
    }

    clickedSettings() {
        this.navCtrl.push(SettingsPage);
    }

    clickedChart() {
        this.navCtrl.setRoot(GrowthChartPage);
    }

    clickedNotifications() {
        this.navCtrl.push(NotificationsPage);
    }

}



