    //growth chart skip popup

    skipGrowthChart() {

        window.localStorage.setItem("skipPopup", 'done');

        let alert = this.alertCtrl.create({
            //title: 'NOTICE',
            subTitle: 'This app provides easy to use growth charts to track your child' + '\'s' + ' ' + 'growth. You can enter past measurements as well as any new measurements.',
            cssClass: 'chartSkipAlert',
            buttons: [
                {
                    text: 'SKIP',
                    cssClass: 'skip-button',
                    handler: data => {
                        this.navCtrl.push('MainmenuPage');
                    }
                },

                {
                    text: 'OK',
                    handler: data => {
                        // this.openModalPoup(); // show measurements popup
                    }
                }

            ]

        });
        alert.present();
    }

    //growth chart edit popup

    openModalPoup() {
        let myModal = this.modalCtrl.create(PopupModalPage);
        myModal.present();
    }


    chartEditPopup() {
        var alert = this.alertCtrl.create({
            title: 'MEASUREMENT',
            cssClass: 'editChartAlert',
            inputs: [
                {
                    name: 'Baby',
                    placeholder: 'Name',
                    type: 'text'
                },
                {
                    name: 'dates',
                    placeholder: 'Date of measurement',
                    type: 'date'
                },
                {
                    name: 'babyWeight',
                    placeholder: 'Weight in Kg'
                },
                {
                    name: 'babyLength',
                    placeholder: 'Length in cm'
                },
                {
                    name: 'babyHead',
                    placeholder: 'Head circumference in cm'
                },
            ],
            buttons: [
                {
                    text: 'Cancel',
                    role: 'cancel',
                    handler: data => {
                        console.log('cancel clicked');
                    }
                },
                {
                    text: 'Save',
                    handler: data => {
                        if (data.dates === " " && data.babyWeight === " " && data.babyLength === " " && data.babyHead === " ") {
                            console.log("jjjjjjjj===========");
                            this.chartEditPopup();
                        } else {
                            console.log("added===========", data.babayWeight);
                        }
                    }
                }

            ]

        });
        alert.present();
    }

    showError(err) {

        let alert = this.alertCtrl.create({
            title: 'ERROR',
            message: err,
            cssClass: 'childNoticeAlert',
            buttons: [
                {
                    text: 'Ok',
                    handler: data => {
                        //this.navCtrl.push('InviteUsersPage');
                    }
                }

            ]

        });
        alert.present();
    }


    clickedMemory() {
        if (window.localStorage.getItem('memoryAdded')) {
            this.navCtrl.push(MemoriesListPage);
        } else {
            this.navCtrl.push(MemoriesPage);
        }
    }

    clickedResources() {
        this.navCtrl.push(ResourcesPage);
    }

    clickedSettings() {
        this.navCtrl.push(SettingsPage);
    }

    clickedNotifications() {
        this.navCtrl.push(NotificationsPage);
    }


