import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, ModalController } from 'ionic-angular';
import {PopupModalPage} from "../popup-modal/popup-modal";
import {GoogleAnalytics} from "@ionic-native/google-analytics";

/**
 * Generated class for the MeasurementsRecordPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@Component({
  selector: 'page-measurements-record',
  templateUrl: 'measurements-record.html',
})
export class MeasurementsRecordPage {

  constructor(public navCtrl: NavController, public navParams: NavParams, public modalCtrl: ModalController,public ga: GoogleAnalytics) {
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad MeasurementsRecordPage');
      this.ga.trackView("Measurements Record Screen")

  }

    addEntry () {
     let myModal = this.modalCtrl.create(PopupModalPage);
     myModal.present();
    }
}



