import {Component} from '@angular/core';
import {IonicPage, NavController, NavParams, MenuController} from 'ionic-angular';
import { NotificationsPage} from "../notifications/notifications";
import {GoogleAnalytics} from "@ionic-native/google-analytics";
import { MemoriesPage} from "../memories/memories";
import { ResourcesPage} from "../resources/resources";
import { SettingsPage} from "../settings/settings";
import { GrowthChartPage} from "../growth-chart/growth-chart";
import { GlobalProvider} from "../../providers/global/global";
import { ApiProvider} from "../../providers/api/api";
import { MemoriesListPage} from "../memories-list/memories-list";

/**
 * Generated class for the FaqPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@Component({
    selector: 'page-faq',
    templateUrl: 'faq.html',
})
export class FaqPage {

    public iconName1 = "ios-arrow-down";
    public iconName2 = "ios-arrow-down";
    public iconName3 = "ios-arrow-down";

    public takingPartClicks = 0;
    public aboutAppClicks = 0;
    public aboutStudyClicks = 0;

    public showTakingPartDetails = false;
    public showAboutappDetails = false;
    public showAboutStudyDetails = false;



    constructor(public navCtrl: NavController,public globalProvider:GlobalProvider, public apiProvider:ApiProvider, public navParams: NavParams, public menuCtrl: MenuController,public ga: GoogleAnalytics) {
    }

    ionViewWillEnter() {
        console.log('ionViewDidLoad FaqPage');
        this.ga.trackView("FAQ Screen");

        this.apiProvider.trackPage("FAQ Screen")
            .then((res) => {

                }, (err) => {

                }
            );

/////////////////////////////// check new notifications available ///////////////////////////////////

        window.localStorage.removeItem("unreadNotifications");

        this.apiProvider.getNotifications()
            .then((res) => {
                    if(window.localStorage.getItem("unreadNotifications") == 'yes'){
                        this.globalProvider.newNotificitations = true;
                    }else{
                        this.globalProvider.newNotificitations = false;
                    }

                }, (err) => {
                    //this.loading.dismiss();
                    //this.showError(err);
                    //this.navCtrl.push(ChildRegistrationPage);
                }
            );
        //////////////////////////////////////////////////////////////////////////
    }

    takingPart() {

        this.takingPartClicks += 1;
        if(this.takingPartClicks != 1){
            this.takingPartClicks = 0;
        };
        if(this.takingPartClicks == 1){
            this.showTakingPartDetails = true;
            this.iconName1 = "ios-arrow-up";
        } else {
            this.showTakingPartDetails = false;
            this.iconName1 = "ios-arrow-down";
        }
    }

    aboutApp() {

        this.aboutAppClicks += 1;
        if(this.aboutAppClicks != 1){
            this.aboutAppClicks = 0;
        };
        if(this.aboutAppClicks == 1){
            this.showAboutappDetails = true;
            this.iconName2 = "ios-arrow-up";
        } else {
            this.showAboutappDetails = false;
            this.iconName2 = "ios-arrow-down";
        }
    }

    aboutStudy() {

        this.aboutStudyClicks += 1;
        if(this.aboutStudyClicks != 1){
            this.aboutStudyClicks = 0;
        };
        if(this.aboutStudyClicks == 1){
            this.showAboutStudyDetails = true;
            this.iconName3 = "ios-arrow-up";
        } else {
            this.showAboutStudyDetails = false;
            this.iconName3 = "ios-arrow-down";
        }
    }

    toggleMenu() {
        this.menuCtrl.toggle();
    }

    clickedNotifications() {
    this.navCtrl.push(NotificationsPage);
    }

    clickedChart(){
        this.navCtrl.push(GrowthChartPage);

    }

    clickedMemory(){
        if (window.localStorage.getItem('memoryAdded')) {
            this.navCtrl.push(MemoriesListPage);
        } else {
            this.navCtrl.push(MemoriesPage);
        }

    }

    clickedResources(){
        this.navCtrl.push(ResourcesPage);

    }

    clickedSettings() {
        this.navCtrl.push(SettingsPage);

    }


}



