import {Component} from '@angular/core';
import {IonicPage, NavController, NavParams} from 'ionic-angular';
import {TipsDetailsPage} from "../tips-details/tips-details";
import {GoogleAnalytics} from "@ionic-native/google-analytics";
import {NotificationsPage} from "../notifications/notifications";
import {TipsEatingPage} from "../tips-eating/tips-eating";
import {TipsActivityPage} from "../tips-activity/tips-activity";
import {TipsGrowthchartPage} from "../tips-growthchart/tips-growthchart";
import {ApiProvider} from "../../providers/api/api";

/**
 * Generated class for the TipsPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@Component({
    selector: 'page-tips',
    templateUrl: 'tips.html',
})
export class TipsPage {

    constructor(public navCtrl: NavController, public navParams: NavParams, public ga: GoogleAnalytics,public apiProvider: ApiProvider) {
    }

    ionViewDidLoad() {
        console.log('ionViewDidLoad TipsPage');
        this.ga.trackView("Tips Screen");

        this.apiProvider.trackPage("Tips Screen")
            .then((res) => {

                }, (err) => {

                }
            );

    }

    eating() {
        this.navCtrl.push(TipsEatingPage);

    }

    activity() {
        this.navCtrl.push(TipsActivityPage);

    }

    growthChart() {
        this.navCtrl.push(TipsGrowthchartPage);

    }

    showTips() {
        this.navCtrl.push(TipsDetailsPage);
    }

}




