import {Component} from '@angular/core';
import {IonicPage, NavController, NavParams} from 'ionic-angular';
import {FirstAidDangerPage} from "../first-aid-danger/first-aid-danger";
import {FirstAidCprUnderOneyearPage} from "../first-aid-cpr-under-oneyear/first-aid-cpr-under-oneyear";
import {FirstAidCprOverOneyearPage} from "../first-aid-cpr-over-oneyear/first-aid-cpr-over-oneyear";
import {GoogleAnalytics} from "@ionic-native/google-analytics";
import { ApiProvider} from "../../providers/api/api";

/**
 * Generated class for the FirstAidCprPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@Component({
    selector: 'page-first-aid-cpr',
    templateUrl: 'first-aid-cpr.html',
})
export class FirstAidCprPage {

    constructor(public navCtrl: NavController, public navParams: NavParams,public ga: GoogleAnalytics, public apiProvider: ApiProvider) {
    }

    ionViewDidLoad() {
        console.log('ionViewDidLoad FirstAidCprPage');
        this.ga.trackView("First Aid CPR Screen");

        this.apiProvider.trackPage("First Aid CPR Screen")
            .then((res) => {

                }, (err) => {

                }
            );

    }

    overOneYear() {
        this.navCtrl.push(FirstAidCprOverOneyearPage);

    }

    underOneYear() {
        this.navCtrl.push(FirstAidCprUnderOneyearPage);

    }

}



