import {Component} from '@angular/core';
import {
    IonicPage,
    NavController,
    NavParams,
    LoadingController,
    AlertController,
    MenuController,
    Platform
} from 'ionic-angular';
import {MemoriesPage} from "../memories/memories";
import {ResourcesPage} from "../resources/resources";
import {SettingsPage} from "../settings/settings";
import {GrowthChartPage} from "../growth-chart/growth-chart";
import {MeasurementsRecordPage} from "../measurements-record/measurements-record";
import {ChildRegistrationPage} from "../child-registration/child-registration";
import {NotificationsPage} from "../notifications/notifications";
import {GoogleAnalytics} from "@ionic-native/google-analytics";
import {GlobalProvider} from "../../providers/global/global";
import {ApiProvider} from "../../providers/api/api";
import {MemoriesListPage} from "../memories-list/memories-list";

declare var window: any

/**
 * Generated class for the MyChildrenPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@Component({
    selector: 'page-my-children',
    templateUrl: 'my-children.html',
})
export class MyChildrenPage {

    public buttonColor2: string = 'light';
    public buttonColor3: string = 'light';
    public buttonColor4: string = 'light';
    public babyName: any;
    public networkConnected = true;

    public childImage: any;
    public childList: any;
    loading: any;


    data: Array<{ title: string, subtitleName: string, icon: string, showDetails: boolean }> = [];
    childData: Array<{ id: string; name: string, photo: string }> = [];
    titles: Array<{ subtitle: string }> = [];

    public childrenList: Array<{ nid: number, first_name: string, dateOfBirth: string, profilePhoto: string, term: string, household: string }> = [];


    constructor(public navCtrl: NavController, public apiProvider: ApiProvider, public navParams: NavParams, public menuCtrl: MenuController, public ga: GoogleAnalytics, public globalProvider: GlobalProvider, public platform: Platform, public alertCtrl: AlertController, public loadingCtrl: LoadingController) {

        this.childImage = "assets/imgs/child.png";

        // @ts-ignore
        this.data = [
            {
                title: "Weight",
                icon: 'ios-arrow-down',
                showDetails: false
            },
            {
                title: "Length",
                icon: 'ios-arrow-down',
                showDetails: false
            },
            {
                title: "Head Circumference",
                icon: 'ios-arrow-down',
                showDetails: false
            },
            {
                title: "",
                icon: '',
                showDetails: false
            }

        ];

        // @ts-ignore
        this.titles = [
            {
                "subtitle": "View Chart",
            },
            // {
            //     "subtitle": "View Records"
            // }

        ];

        // @ts-ignore

        this.childData.push(
            {
                id: "child1",
                name: this.globalProvider.childFirstName,
                photo: window.localStorage.getItem("childPhoto")
            }
        );


        // for(let i = 0; i < 3; i++ ){
        //     this.data.push({
        //         title: 'Title '+i,
        //         details: 'Lorem ipsum dolor sit amet, consectetur adipisicing elit, sed do eiusmod tempor incididunt ut labore et dolore magna aliqua. Ut enim ad minim veniam, quis nostrud exercitation ullamco laboris nisi ut aliquip ex ea commodo consequat. Duis aute irure dolor in reprehenderit in voluptate velit esse cillum dolore eu fugiat nulla pariatur. Excepteur sint occaecat cupidatat non proident, sunt in culpa qui officia deserunt mollit anim id est laborum.',
        //         icon: 'ios-arrow-down',
        //         showDetails: false
        //     });
        // }

        this.platform.ready().then(() => {

            if (window.cordova && (window.navigator.connection.type === window.Connection.UNKNOWN || window.navigator.connection.type === window.Connection.NONE)) {
                this.networkConnected = false;
            } else {
                this.networkConnected = true;
            }

        });

        if (this.networkConnected === false) {
            this.showError("Check your internet connection and try again.");
        } else {

            /////// track page //////
            this.apiProvider.trackPage("My Children Screen")
                .then((res) => {

                    }, (err) => {

                    }
                );

            this.presentLoading();

            setTimeout(() => {
                this.apiProvider.getChildrenList()
                    .then((res) => {
                            console.log("children list" + JSON.stringify(res));
                            for (let i = 0; i < JSON.parse(JSON.stringify(res)).length; i++) {
                                this.childrenList.push(
                                    {
                                        first_name: JSON.parse(JSON.stringify(res))[i].title,
                                        dateOfBirth: JSON.parse(JSON.stringify(res))[i].field_date_of_birth,
                                        term: JSON.parse(JSON.stringify(res))[i].field_term,
                                        profilePhoto: JSON.parse(JSON.stringify(res))[i].field_upload_image,
                                        household: JSON.parse(JSON.stringify(res))[i].household,
                                        nid: JSON.parse(JSON.stringify(res))[i].nid
                                    });
                            }

                            //window.localStorage.setItem("childrenList", JSON.stringify(this.childrenList));
                            this.loading.dismiss();


                            if (this.childrenList[0].profilePhoto == "") {
                                this.childImage = "assets/imgs/child.png";
                                this.childrenList[0].profilePhoto = "assets/imgs/child.png";
                            } else {
                                this.childImage = this.childrenList[0].profilePhoto;
                            }

                            if (this.childrenList.length == 2) {
                                if (this.childrenList[1].profilePhoto == "") {
                                    this.childImage = "assets/imgs/child-1.png";
                                    this.childrenList[0].profilePhoto = "assets/imgs/child.png";
                                    this.childrenList[1].profilePhoto = "assets/imgs/child-1.png";
                                } else {
                                    this.childImage = this.childrenList[1].profilePhoto;
                                }
                            }


                            if (this.childrenList.length == 3) {

                                if (this.childrenList[2].profilePhoto == "") {
                                    this.childImage = "assets/imgs/child-2.png";
                                    this.childrenList[0].profilePhoto = "assets/imgs/child.png";
                                    this.childrenList[1].profilePhoto = "assets/imgs/child-1.png";
                                    this.childrenList[2].profilePhoto = "assets/imgs/child-2.png";
                                } else {
                                    this.childImage = this.childrenList[2].profilePhoto;
                                }
                            }

                            if (this.childrenList[0].profilePhoto == "") {
                                this.childImage = "assets/imgs/child.png";
                                this.childrenList[0].profilePhoto = "assets/imgs/child.png";
                            } else {
                                this.childImage = this.childrenList[0].profilePhoto;
                            }

                            this.babyName = this.childrenList[0].first_name;


                        }, (err) => {

                            this.loading.dismiss();
                            //this.showError(err);
                            //this.navCtrl.push(ChildRegistrationPage);
                        }
                    );
            }, 1000);


        }
    }

    titleCliked(data) {
        if (data.showDetails) {
            data.showDetails = false;
            data.icon = 'ios-arrow-down';
        } else {
            data.showDetails = true;
            data.icon = 'ios-arrow-up';
        }
    }

    subTitleCliked(sub, titl) {
        if (sub.subtitle == "View Chart") {
            if (titl.title == "Weight") {
                this.navCtrl.setRoot(GrowthChartPage, {childChartType: 'weight'});
            } else if (titl.title == "Length") {
                this.navCtrl.setRoot(GrowthChartPage, {childChartType: 'length'});
            } else if (titl.title == "Head Circumference") {
                this.navCtrl.setRoot(GrowthChartPage, {childChartType: 'head'});
            }
        } else if (sub.subtitle == "View Records") {
            this.navCtrl.push(MeasurementsRecordPage);
        }
    }

    ionViewWillEnter() {

        console.log('ionViewDidLoad MyChildrenPage');
        this.ga.trackView("My Children Screen");


        /////////////////////////////// check new notifications available ///////////////////////////////////

        window.localStorage.removeItem("unreadNotifications");

        this.apiProvider.getNotifications()
            .then((res) => {
                    if(window.localStorage.getItem("unreadNotifications") == 'yes'){
                        this.globalProvider.newNotificitations = true;
                    }else{
                        this.globalProvider.newNotificitations = false;
                    }

                }, (err) => {
                    //this.loading.dismiss();
                    this.showError(err);
                    //this.navCtrl.push(ChildRegistrationPage);
                }
            );
        //////////////////////////////////////////////////////////////////////////

    }


    openMenu() {
        this.menuCtrl.open();
    }

    closeMenu() {
        this.menuCtrl.close();
    }

    toggleMenu() {
        this.menuCtrl.toggle();
    }

    clickedMemory() {
        if (window.localStorage.getItem('memoryAdded')) {
            this.navCtrl.push(MemoriesListPage);
        } else {
            this.navCtrl.push(MemoriesPage);
        }
    }

    clickedResources() {
        this.navCtrl.push(ResourcesPage);
    }

    clickedSettings() {
        this.navCtrl.push(SettingsPage);
    }

    clickedChart() {
        this.navCtrl.setRoot(GrowthChartPage);
    }

    clickedNotifications() {
        this.navCtrl.push(NotificationsPage);
    }


    child(val, id) {


        document.getElementById("btn-" + id).style.background = '#3B8175 ';
        document.getElementById("btn-" + id).style.color = 'white ';

        if (id == 0) {
            console.log("button id=====" + id)
            console.log("child count =====" + this.childrenList.length);

            document.getElementById("btn-" + id).style.background = '#3B8175 ';
            document.getElementById("btn-" + id).style.color = 'white ';

            if (this.childrenList.length == 2) {
                document.getElementById("btn-0").style.background = '#3B8175 ';
                document.getElementById("btn-0").style.color = 'white ';

                document.getElementById("btn-1").style.background = '#ffffff ';
                document.getElementById("btn-1").style.color = 'black ';

            } else if (this.childrenList.length == 3) {
                document.getElementById("btn-0").style.background = '#3B8175 ';
                document.getElementById("btn-0").style.color = 'white ';

                document.getElementById("btn-1").style.background = '#ffffff ';
                document.getElementById("btn-1").style.color = 'black ';


                document.getElementById("btn-2").style.background = '#ffffff ';
                document.getElementById("btn-2").style.color = 'black ';

            }


        } else if (id == 1) {
            console.log("button id=====" + id)
            console.log("child count =====" + this.childrenList.length);

            document.getElementById("btn-" + id).style.background = '#3B8175 ';
            document.getElementById("btn-" + id).style.color = 'white ';

            if (this.childrenList.length == 2) {
                document.getElementById("btn-1").style.background = '#3B8175 ';
                document.getElementById("btn-1").style.color = 'white ';

                document.getElementById("btn-0").style.background = '#ffffff ';
                document.getElementById("btn-0").style.color = 'black ';


            } else if (this.childrenList.length == 3) {
                document.getElementById("btn-1").style.background = '#3B8175 ';
                document.getElementById("btn-1").style.color = 'white ';

                document.getElementById("btn-0").style.background = '#ffffff ';
                document.getElementById("btn-0").style.color = 'black ';

                document.getElementById("btn-2").style.background = '#ffffff ';
                document.getElementById("btn-2").style.color = 'black ';

            }

        } else if (id == 2) {
            console.log("button id=====" + id)
            console.log("child count =====" + this.childrenList.length);

            document.getElementById("btn-" + id).style.background = '#3B8175 ';
            document.getElementById("btn-" + id).style.color = 'white ';

            if (this.childrenList.length == 2) {
                document.getElementById("btn-2").style.background = '#3B8175 ';
                document.getElementById("btn-2").style.color = 'white ';

                document.getElementById("btn-0").style.background = '#ffffff ';
                document.getElementById("btn-0").style.color = 'black ';

                document.getElementById("btn-1").style.background = '#ffffff ';
                document.getElementById("btn-1").style.color = 'black';

            } else if (this.childrenList.length == 3) {
                document.getElementById("btn-0").style.background = '#ffffff ';
                document.getElementById("btn-0").style.color = 'black ';

                document.getElementById("btn-2").style.background = '#3B8175 ';
                document.getElementById("btn-2").style.color = 'white ';

                document.getElementById("btn-1").style.background = '#ffffff ';
                document.getElementById("btn-1").style.color = 'black ';


            }


        }

        this.babyName = val.first_name;
        console.log("child photo===" + val.profilePhoto);
        if (val.profilePhoto == "") {
            this.childImage = "assets/imgs/child.png";
        } else {
            this.childImage = val.profilePhoto;
        }
    }


    addChild() {
        this.navCtrl.push(ChildRegistrationPage, {hideBackBtn: 'false'});

    }


    presentLoading() {

        this.loading = this.loadingCtrl.create({
            content: 'Please wait...'
        });

        this.loading.present();

    }


    showAlert(err) {

        let alert = this.alertCtrl.create({
            title: 'ERROR',
            message: err.error.message,
            cssClass: 'childNoticeAlert',
            buttons: [
                {
                    text: 'Ok',
                    handler: data => {
                        //this.navCtrl.push('InviteUsersPage');
                    }
                }

            ]

        });
        alert.present();
    }

    showError(err) {

        let alert = this.alertCtrl.create({
            title: 'ERROR',
            message: err,
            cssClass: 'childNoticeAlert',
            buttons: [
                {
                    text: 'Ok',
                    handler: data => {
                        //this.navCtrl.push('InviteUsersPage');
                    }
                }

            ]

        });
        alert.present();
    }


}



