import {Component} from '@angular/core';
import {IonicPage, NavController, NavParams, AlertController} from 'ionic-angular';
import {Calendar} from "@ionic-native/calendar";

/**
 * Generated class for the EventsModalPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@Component({
    selector: 'page-events-modal',
    templateUrl: 'events-modal.html',
})
export class EventsModalPage {

    event = {title: "", location: "", message: "", startDate: "", endDate: ""};


    constructor(public navCtrl: NavController, public navParams: NavParams, public alertCtrl: AlertController, private calendar: Calendar) {
    }

    ionViewDidLoad() {
        console.log('ionViewDidLoad EventsModalPage');
    }

    save() {
        this.calendar.createEvent(this.event.title, this.event.location, this.event.message, new Date(this.event.startDate), new Date(this.event.endDate)).then(
            (msg) => {
                let alert = this.alertCtrl.create({
                    title: 'Success!',
                    cssClass: 'childNoticeAlert',
                    subTitle: 'Event saved successfully',
                    buttons: ['OK']
                });
                alert.present();
                this.navCtrl.pop();
            },
            (err) => {
                let alert = this.alertCtrl.create({
                    title: 'Failed!',
                    cssClass: 'childNoticeAlert',
                    subTitle: err,
                    buttons: ['OK']
                });
                alert.present();
            }
        );
    }

}



