import { Component } from '@angular/core';
import {IonicPage, NavController, NavParams, Platform} from 'ionic-angular';
import {SplashScreen} from '@ionic-native/splash-screen';
import {WelcomePage} from "../welcome/welcome";
import {el} from "@angular/platform-browser/testing/src/browser_util";
import { GrowthChartPage } from "../growth-chart/growth-chart";
import {ResourcesPage} from "../resources/resources";

/**
 * Generated class for the Splashscreen2Page page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */


@Component({
  selector: 'page-splashscreen2',
  templateUrl: 'splashscreen2.html',
})
export class Splashscreen2Page {

  constructor(
      public navCtrl: NavController,
      public  _SplashScreen: SplashScreen,
      public navParams: NavParams,
      public _platform: Platform
             )
  {
      this._platform.ready().then(() => {
          setTimeout(() => {
              this._SplashScreen.hide();
          }, 3000);
      });

      this.navCtrl = navCtrl;

  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad Splashscreen2Page');
    setTimeout(() => {

        if(window.localStorage.getItem("registerDone") != null ) {
            this.navCtrl.setRoot(ResourcesPage);
        }else{
            this.navCtrl.push(WelcomePage)
        }

    }, 4000);

  }

}



