import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import {GoogleAnalytics} from "@ionic-native/google-analytics";
import { ApiProvider} from "../../providers/api/api";

/**
 * Generated class for the FirstAidCprUnderOneyearPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@Component({
  selector: 'page-first-aid-cpr-under-oneyear',
  templateUrl: 'first-aid-cpr-under-oneyear.html',
})
export class FirstAidCprUnderOneyearPage {

    public iconNameD = "ios-arrow-down";

    public dangerClicks = 0;
    public showDetails = false;

  constructor(public navCtrl: NavController, public navParams: NavParams,public ga: GoogleAnalytics, public apiProvider:ApiProvider) {
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad FirstAidCprUnderOneyearPage');
      this.ga.trackView("First Aid CPR Under Oneyear Screen");

      this.apiProvider.trackPage("CPR Under One year Screen")
          .then((res) => {

              }, (err) => {

              }
          );

  }

    generalSigns() {

        this.dangerClicks += 1;
        if(this.dangerClicks != 1){
            this.dangerClicks = 0;
        };
        if(this.dangerClicks == 1){
            this.showDetails = true;
            this.iconNameD = "ios-arrow-up";
        } else {
            this.showDetails = false;
            this.iconNameD = "ios-arrow-down";
        }
    }

}



