import {Component} from '@angular/core';
import {AlertController, IonicPage, NavController, NavParams, LoadingController} from 'ionic-angular';
import {GoogleAnalytics} from "@ionic-native/google-analytics";
import {MemoriesListPage} from "../memories-list/memories-list";
import {ApiProvider} from "../../providers/api/api";
import {el} from "@angular/platform-browser/testing/src/browser_util";
import {GrowthChartPage} from "../growth-chart/growth-chart";
import {MemoriesPage} from "../memories/memories";
import {ResourcesPage} from "../resources/resources";
import {SettingsPage} from "../settings/settings";
import {NotificationsPage} from "../notifications/notifications";

/**
 * Generated class for the MemoryDetailsPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@Component({
    selector: 'page-memory-details',
    templateUrl: 'memory-details.html',
})
export class MemoryDetailsPage {
    public commentsHide = true;
    public clicks = 0;

    public memoryDetails: any;
    public memoryImage: any;
    public description: any;
    public uploaderName: any;
    public uploaderImage: any;
    public date: any;
    public commentCount: number;
    public likes: any;
    public comment: any
    public nid: any
    public message: string = '';

    loading: any;

    public commentsList: Array<{ description: string, uploaderName: string, uploaderImage: string, date: string }> = [];


    constructor(public navCtrl: NavController, public navParams: NavParams, public ga: GoogleAnalytics, public loadingCtrl: LoadingController, public apiProvider: ApiProvider, public alertCtrl: AlertController,) {

        this.memoryDetails = navParams.get('memoryDetails');
        this.memoryImage = this.memoryDetails.memoryImage;
        this.description = this.memoryDetails.description;
        this.nid = this.memoryDetails.nid;
        if (this.memoryDetails.uploaderImage == '') {
            this.uploaderImage = "assets/imgs/parent.png";
        } else {
            this.uploaderImage = this.memoryDetails.uploaderImage;
        }
        this.uploaderName = this.memoryDetails.uploaderName;
        this.date = this.memoryDetails.date;
        this.commentCount = this.memoryDetails.comments;
        if (this.commentCount == 0) {
            this.commentsHide = true;
        }
        this.likes = this.memoryDetails.likes;
    }

    ionViewDidLoad() {
        console.log('ionViewDidLoad MemoryDetailsPage');
        this.ga.trackView("Memory Details Screen")
    }

    showComments() {
        console.log("memory comments");
        //this.presentLoading();

        this.commentsList = [];

        ///// get comments  /////////
        setTimeout(() => {
            this.apiProvider.getMemoryComment(this.nid)
                .then((res) => {
                        //this.loading.dismiss();
                        console.log("memory comments list======" + JSON.stringify(res));
                        for (let i = 0; i < JSON.parse(JSON.stringify(res)).length; i++) {
                            this.commentsList.push(
                                {
                                    description: JSON.parse(JSON.stringify(res))[i].comment_body,
                                    uploaderName: JSON.parse(JSON.stringify(res))[i].field_first_name,
                                    uploaderImage: JSON.parse(JSON.stringify(res))[i].user_picture,
                                    date: JSON.parse(JSON.stringify(res))[i].created
                                });
                        }
                        //this.loading.dismiss();


                    }, (err) => {
                        console.log(JSON.stringify(err));
                        //alert("========");
                        //this.loading.dismiss();
                        //this.showAlert(JSON.stringify(err));
                        //this.navCtrl.push(ChildRegistrationPage);
                    }
                );
        }, 500);

        if (this.commentCount != 0) {
            this.clicks += 1;
            if (this.clicks != 1) {
                this.clicks = 0;
            }

            if (this.clicks == 1) {
                this.commentsHide = false;
            } else {
                this.commentsHide = true;
            }
        }

    }

    commentAdded(val) {
        this.comment = val;
    }

    sendComment() {

        // this.commentCount += 1;

        if (this.comment != null) {
            //this.presentLoading();

            ///// add comment /////////
            setTimeout(() => {
                this.apiProvider.addMemoryComment(this.comment, this.nid)
                    .then((res) => {
                            //this.loading.dismiss();
                            console.log("ressssss" + JSON.stringify(res))
                            this.message = '';
                            this.commentCount = JSON.parse(JSON.stringify(res)).field_comment[0].comment_count;
                            this.showComments();
                        }, (err) => {
                            //alert(JSON.stringify(err));
                            //alert("========");
                            //this.loading.dismiss();
                            //this.showAlert(JSON.stringify(err));
                            //this.navCtrl.push(ChildRegistrationPage);
                        }
                    );
            }, 1000);
        } else {

            this.showAlert('Please add your comment.');
        }
    }


    addLoves() {

        ///// add loves /////////
        setTimeout(() => {
            this.apiProvider.addMemoryLoves(this.nid)
                .then((res) => {
                        //this.loading.dismiss();
                        this.likes = JSON.parse(JSON.stringify(res)).field_like[0].value;
                    }, (err) => {
                        //alert(JSON.stringify(err));
                        //alert("========");
                        //this.loading.dismiss();
                        //this.showAlert(JSON.stringify(err));
                        //this.navCtrl.push(ChildRegistrationPage);
                    }
                );
        }, 500);

    }

    showAlert(msg) {

        let alert = this.alertCtrl.create({
            title: 'SHTG',
            message: msg,
            cssClass: 'childNoticeAlert',
            buttons: [
                {
                    text: 'Ok',
                    handler: data => {
                        //this.navCtrl.push('InviteUsersPage');
                    }
                }

            ]

        });
        alert.present();
    }

    presentLoading() {

        this.loading = this.loadingCtrl.create({
            content: 'Please wait...'
        });

        this.loading.present();

    }


    clickedChart(){
        this.navCtrl.push(GrowthChartPage);

    }

    clickedMemory() {
        if (window.localStorage.getItem('memoryAdded')) {
            this.navCtrl.push(MemoriesListPage);
        } else {
            this.navCtrl.push(MemoriesPage);
        }
    }

    clickedResources() {
        this.navCtrl.push(ResourcesPage);
    }

    clickedSettings() {
        this.navCtrl.push(SettingsPage);
    }

    clickedNotifications() {
        this.navCtrl.push(NotificationsPage);
    }
}



