import { HttpClient } from '@angular/common/http';
import { AlertController, NavController, NavParams, App } from 'ionic-angular';
import { Injectable } from '@angular/core';
import { Http } from '@angular/http';
import { Device } from '@ionic-native/device';
import { ApiProvider } from '../api/api'
import { GlobalProvider } from '../global/global'
import { Push, PushObject, PushOptions, NotificationEventResponse } from '@ionic-native/push';
import { NotificationsPage} from "../../pages/notifications/notifications";
import { Component, ViewChild } from '@angular/core';


declare var window: any;

/*
  Generated class for the NotificationsProvider provider.

  See https://angular.io/guide/dependency-injection for more info on providers
  and Angular DI.
*/
@Injectable()
export class NotificationsProvider {
    nav: NavController;

    constructor(public http: HttpClient,
                public api: ApiProvider,
                public push: Push,
                public app: App,
                public device: Device,
                public global: GlobalProvider,
                public alertCtrl: AlertController

    ) {
        console.log('Hello NotificationsProvider Provider');
        this.nav = app.getActiveNav();
    }

    init() {
        console.log('NotificationProvider init');
        if (window.cordova) {
            this.initLocalNotification();
            this.initPushNotification();
        }
        //this.updateNotifications();
    }

    initLocalNotification() {
        //if ('android' != window.cordova.platformId) {
        window.cordova.plugins.notification.local.on("trigger", this.onLocalTriggered, this);
        window.cordova.plugins.notification.local.on('click', this.onLocalClicked, this);
        window.cordova.plugins.notification.hasPermission(function(data){ if(!data.isEnabled){ window.cordova.plugins.notification.grantPermission(); } });
        //}
    }


    initPushNotification() {
        const options: PushOptions = {
            android: {
                // senderID: '600992923443',
                // forceShow: true,
                icon: 'icon',
            },
            ios: {
                // senderID: '600992923443',
                alert: true,
                badge: true,
                sound: true,
                // gcmSandbox: true
                fcmSandbox: true
            },
            windows: {}
        };

        const pushObject: PushObject = this.push.init(options);

        pushObject.on('registration').subscribe(this.onPushRegistered);
        pushObject.on('notification').subscribe(this.onPushNotified);
        pushObject.on('error').subscribe(this.onPushError);
    }

    onPushError = (data: any) => {
        error => console.error('Error with Push plugin' + error)
    }

    onLocalTriggered = (data: any) => {
        console.log('onLocalTriggered', data);

        if ('android' == window.cordova.platformId) {
            console.log('onLocalTriggered is ignored on Android')
            return;
        }

        //this.updateNotifications();
    }

    onLocalClicked = (data: any) => {
        console.log('onLocalClicked', data)

        if ('android' == window.cordova.platformId) {
            console.log('onLocalClicked is ignored on Android')
            return;
        }
    }

    onPushRegistered = (data: any) => {
        console.log(`device token:${data.registrationId}, uuid:${this.device.uuid}`);
        //alert(data.registrationId);
        this.global.notificationToken = data.registrationId;
        console.log("ttttttt",data.registrationId);
        // let device: DeviceToken = {
        //     device_id: this.device.uuid,
        //     token: data.registrationId
        // }
        // this.api.postDevice(device)
        //     .then(res => {
        //         console.log('postDevice', res)
        //     })
        //     .catch(err => {
        //         console.log('postDevice err', err)
        //     })
    }

    onPushNotified = (data: NotificationEventResponse) => {
        console.log('onPushNotified',data);

        //this.updateNotifications();
        if (data.additionalData.foreground) {
            this.localNoti(data.title, data.message);
            this.nav.push(NotificationsPage);

        }else{
            this.nav.push(NotificationsPage);
        }


        // if (data.additionalData.foreground){
        //     let confirmAlert = this.alertCtrl.create({
        //         title: data.title,
        //         message: data.message,
        //         buttons: [{
        //             text: 'Ignore',
        //             role: 'cancel'
        //         }, {
        //             text: 'View',
        //             handler: () => {
        //                 this.nav.push(NotificationsPage);
        //             }
        //         }]
        //     });
        //     confirmAlert.present();
        // }else {
        //     console.log('on else');
        //     this.nav.push(NotificationsPage);
        //     console.log('Push notification clicked');
        // }

        // if(data.additionalData.coldstart){
        //     //redirecturl will be additional key in your payload to hold the relative url for redirection
        //     this.nav.push(NotificationsPage);
        //
        // }
    }


    localNoti = (title, text) => {
        console.log(`localNoti title: ${title} text: ${text}`)

        window.cordova.plugins.notification.local.schedule({
            title,
            text,
            icon: 'res://icon'
        })
    }

}



