import {Component} from '@angular/core';
import {IonicPage, NavController, NavParams, LoadingController, Platform, AlertController} from 'ionic-angular';
import {MemoryDetailsPage} from "../memory-details/memory-details";
import {MemoriesPage} from "../memories/memories";
import {GoogleAnalytics} from "@ionic-native/google-analytics";
import {ApiProvider} from "../../providers/api/api";
import {GrowthChartPage} from "../growth-chart/growth-chart";
import {ResourcesPage} from "../resources/resources";
import {SettingsPage} from "../settings/settings";
import {NotificationsPage} from "../notifications/notifications";

declare var window: any

/**
 * Generated class for the MemoriesListPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@Component({
    selector: 'page-memories-list',
    templateUrl: 'memories-list.html',
})
export class MemoriesListPage {

    //public memoryData: any;
    public memoryList: Array<{ description: string, uploaderName: string, uploaderImage: string, memoryImage: string, date: string, comments: number, nid: number, likes: number }> = [];


    public memoryImage: any;
    public description: any;
    public uploaderName: any;
    public uploaderImage: any;
    public likes: any;
    public comments: any;
    public date: any;
    public data: any;
    loading: any;
    public networkConnected = true;



    constructor(public navCtrl: NavController,public platform: Platform,public alertCtrl: AlertController, public navParams: NavParams, public ga: GoogleAnalytics, public apiProvider: ApiProvider, public loadingCtrl: LoadingController) {

        this.platform.ready().then(() => {

            if (window.cordova && (window.navigator.connection.type === window.Connection.UNKNOWN || window.navigator.connection.type === window.Connection.NONE)) {
                this.networkConnected = false;
            } else {
                this.networkConnected = true;
            }

        });

        this.comments = "0";
        this.likes = "0";

        if (window.localStorage.getItem("parentPhoto") == null) {
            this.uploaderImage = "assets/imgs/parent.png";
        } else {
            this.uploaderImage = window.localStorage.getItem("parentPhoto");
        }
        this.uploaderName = window.localStorage.getItem("parentFirstName") + " " + window.localStorage.getItem("parentLastName")

        //console.log("memory data" + this.memoryData)
    }

    ionViewDidLoad() {
        console.log('ionViewDidLoad MemoriesListPage');
        this.ga.trackView("Memory List Screen");

        this.apiProvider.trackPage("Memories List Screen")
            .then((res) => {

                }, (err) => {

                }
            );

        this.presentLoading();

        setTimeout(() => {
            this.apiProvider.getMemoryList()
                .then((res) => {
                    //alert("memory list==========="+JSON.stringify(res));

                    for (let i = 0; i < JSON.parse(JSON.stringify(res)).length; i++) {
                            this.memoryList.push(
                                {
                                    description: JSON.parse(JSON.stringify(res))[i].body,
                                    uploaderName: JSON.parse(JSON.stringify(res))[i].field_first_name,
                                    uploaderImage: JSON.parse(JSON.stringify(res))[i].user_picture,
                                    memoryImage: JSON.parse(JSON.stringify(res))[i].field_upload_image,
                                    date: JSON.parse(JSON.stringify(res))[i].created,
                                    likes: JSON.parse(JSON.stringify(res))[i].field_like,
                                    comments: JSON.parse(JSON.stringify(res))[i].comment_count,
                                    nid:JSON.parse(JSON.stringify(res))[i].nid
                                });
                        }


                    window.localStorage.setItem("memoryTest", JSON.stringify(this.memoryList));


                    this.loading.dismiss();
                    }, (err) => {

                        this.loading.dismiss();
                        //this.showError(err);
                        //this.navCtrl.push(ChildRegistrationPage);
                    }
                );
        }, 1000);


    }

    presentLoading() {

        this.loading = this.loadingCtrl.create({
            content: 'Loading...'
        });

        this.loading.present();

        setTimeout(() => {
            this.loading.dismiss();
            if (!this.networkConnected) {
                this.showAlert();
            }
        }, 2000);

    }

    memoryDetailsView(val) {
        this.navCtrl.push(MemoryDetailsPage, {memoryDetails: val})
    }

    addMemory() {
        this.navCtrl.push(MemoriesPage);
    }

    showAlert() {

        let alert = this.alertCtrl.create({
            title: 'ERROR',
            message: "Please check your internet connection and try again.",
            cssClass: 'childNoticeAlert',
            buttons: [
                {
                    text: 'Ok',
                    handler: data => {
                        //this.navCtrl.push('InviteUsersPage');
                    }
                }

            ]

        });
        alert.present();
    }


    clickedChart(){
        this.navCtrl.setRoot(GrowthChartPage);

    }

    clickedMemory() {
        if (window.localStorage.getItem('memoryAdded')) {
            this.navCtrl.push(MemoriesListPage);
        } else {
            this.navCtrl.push(MemoriesPage);
        }
    }

    clickedResources() {
        this.navCtrl.push(ResourcesPage);
    }

    clickedSettings() {
        this.navCtrl.push(SettingsPage);
    }

    clickedNotifications() {
        this.navCtrl.push(NotificationsPage);
    }
}



