import {Component} from '@angular/core';
import {IonicPage, NavController, NavParams} from 'ionic-angular';
import {ParentRegistrationPage} from "../parent-registration/parent-registration";
import {GoogleAnalytics} from "@ionic-native/google-analytics";

/**
 * Generated class for the IntroductionPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@Component({
    selector: 'page-introduction',
    templateUrl: 'introduction.html',
})
export class IntroductionPage {

    constructor(public navCtrl: NavController, public navParams: NavParams, public ga: GoogleAnalytics) {
    }

    ionViewDidLoad() {
        console.log('ionViewDidLoad IntroductionPage');
        this.ga.trackView("Introduction Screen");

    }

    startApp() {
        this.navCtrl.push(ParentRegistrationPage)
    }

    skipIntro() {
        this.navCtrl.push(ParentRegistrationPage)
    }

}



